:root {
  --lb-pink: #FC716B;
  --lb-red: #E10736;
  --lb-salmon: #E87D7E;
  --lb-orange: #FF3832;
  --white: #ffffff;
  --black: #000;
}

.App {
  background-color: var(--lb-pink);
  text-align: center;
  color: var(--black);
  min-height: 95vh;
}

.App-header {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  font-size: calc(10px + 2vmin);
}

.App-lb{
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 2%;
}

.App-buttons {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 4%;
}

div .pz-moment__icon .medium .letter-boxed {
  display: flex;
  justify-content: flex-start;
}

.lb-button {
  background-color: var(--black);
  color: var(--white);
  margin-left: 10px;
  margin-right: 10px;
}

.lb-button:hover {
  background-color: var(--lb-pink);
  color: var(--black);
  outline: var(--black) 2px solid;
}

.error-message {
  margin-top: 10px;
  font-weight: bold;
  color: var(--lb-red);
}
footer {
  display: flex;
  justify-content: center;
}

.pph-socials {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 10px;
  font-weight: bold;
  color: var(--black);
}

@keyframes heartbeat {

  0%,
  100% {
    transform: scale(1);
  }

  50% {
    transform: scale(1.15);
  }
}
.yoyo {
  margin-right: 10px;
  animation       : heartbeat 1s infinite;
  transform-origin: center;
}

a:hover {
  color: var(--lb-red);
}